import Vue from 'vue';
import axios from 'axios';
import App from './App.vue';
import router from './router';
import VueAxios from 'vue-axios'; //vue.$httpvue.axios
import VueSocketIO from 'vue-socket.io';
import BootstrapVue from 'bootstrap-vue';
import Loading from 'vue-loading-overlay';
import 'bootstrap/dist/css/bootstrap.css';
import store from './store';
// Import stylesheet
import 'vue-loading-overlay/dist/vue-loading.css';
import {  ValidationObserver, ValidationProvider, extend, configure } from 'vee-validate';
import { required, email, min } from 'vee-validate/dist/rules';  // 
Vue.config.productionTip = false;
axios.defaults.withCredentials = true; //

extend('email', email);
extend('required', required);
extend('confirmPassword', {
  ...required,//
  message: 'Please confirm your password again'
})
extend('min', min);
extend('required-SignUp-Name', {
  ...required,
  message: 'Please fill in your name'
})

extend('required-SignUp-Login-Email', {
  ...required,
  message: 'Please fill in your Email'
})

extend('required-SignUp-Login-Password', {
  ...required,
  message: 'Please fill in your Password'
})
extend('password', {
  validate(value) { //
    const expression = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/;
    const pwdExp = new RegExp(expression);//
    if(pwdExp.test(value)==true) { //true
      return true
    } else {
      return false
    }
  },
  message: `The password should be at least 8 yards in length,and mixed uppercase and lowercase English letters and numbers`
})
extend('required-SignUp-Login-PasswordConfirm', {
  ...required,
  params: ['pwd'],
  validate(value, { pwd }){
    return value === pwd;
  },
  message: `The password you entered for the second time is different, please confirm again`
})
//

//
Vue.component('loading', Loading);
Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);

Vue.use(
  new VueSocketIO({
    debug: true,
    //connection: `http://127.0.0.1:3000`
    connection: `https://traffic-control-system.herokuapp.com:80`
  })
)
  .use(router)
  .use(BootstrapVue)
  .use(VueAxios, axios);

  // Class 
  configure({
    classes: {
      valid: 'is-valid',//bootstrapclass name
      invalid: 'is-invalid'
    }
  });
  
router.beforeEach((to, from, next) => {
  console.log('');
  if(to.meta.requiresAuth === true) {
    const api = `${process.env.VUE_APP_CHECK_API_URL}/api/v1/traffic/Check`;
    axios.post(api).then((res) => {//routerthisthisvue(this.$httpaxios)
      console.log(res);
      if(res.data.success) {
        next();
      } else {
        next({
          path: '/Login'
        })
      }
    });
  } else {
    next();
  }
});


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');

console.log(': ', process.env);